.inner-triangle{
  border-left: 120px solid transparent;
  border-right: 120px solid green;
  border-bottom: 120px solid transparent;
  height: 0;
  width: 0;
  position: absolute;
  right: 0px;
  z-index: 2;
}

button {
  display: block;
  background: none;
  border: none;
  padding: 0;
  font-family: inherit;
  cursor: pointer;
  outline: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  touch-action: manipulation;
}

button:active {
  box-shadow: inset 0px 0px 80px 0px rgba(0,0,0,0.25);
}

#wrapper {
  margin-top:120px;
  display: flex;
  align-items: center;
  justify-content: center;
}



.calculator {
  border-radius: 10px;
  width: 100%;
  height: 550px;
  background: #2e398a;
  display: flex;
  flex-direction: column;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.calculator-display {
  color: white;
  line-height: 130px;
  font-size: 6em;
  border-bottom: 1px solid #6d74ad;
  margin-bottom: 10px;
  margin-left: 20px;
  margin-right: 20px;
  flex: 1;
}

.auto-scaling-text {
  display: inline-block;
}

.calculator-display .auto-scaling-text {
  padding: 0 16px;
  position: absolute;
  right: 0;
  transform-origin: right;
  margin-left: 20px;
  margin-right: 20px;
}

.calculator-keypad {
  height: 400px;
  display: flex;
  justify-content: center;
}

.calculator .input-keys {
  width: 240px;
}

.calculator .function-keys {
  display: flex;
}

.calculator .digit-keys {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap-reverse;
}

.calculator-key {
  width: 70px;
  height: 70px;
  text-align: center;
  line-height: 60px;
  border: 8px solid #2e398a;
  border-radius: 50%;
}
.calculator .function-keys .calculator-key {
  color: white;
  font-size: 1.6em;
}

.calculator .digit-keys .calculator-key {
  font-size: 2.25em;
  color: #9fa5cf;
  background-color: #2e398a;
  border: 1px solid #6d74ad;
  margin: 2.5px;
  margin-top: 5px;
}

.calculator .digit-keys .key-0 {
  width: 145px;
  text-align: left;
  padding-left: 26px;
  border-radius: 36px;
}
.calculator .digit-keys .key-dot {
  padding-top: 0.4em;
}
.calculator .operator-keys .calculator-key {
  color: white;
  font-size: 2.2em;
  background-color: #fadb4b;
}

.calc-key {
  width: 75px;
  height: 75px;
  text-align: center;
  line-height: 60px;
  border: 2px solid #2e398a;
  border-radius: 50%;
  margin: 2.5px;
  margin-bottom: -1px;
}

/* .calculator .function-keys,
.calculator .operator-keys {
} */

.calculator .function-keys .calculator-key {
  background-color: #2e398a;
  height: 70px;
  width: 70px;
  border: 1px solid #fadb4b;
  margin: 2.5px;
}

